<template>
    <main>
        <div class="container">
            <section class="input-area">
                <el-row>
                    <el-col :span="18"><el-input type="text" v-model="target" placeholder="Input"></el-input></el-col>
                    <el-col :span="5":offset="1"> <el-button type="primary" v-on:click="btSearch">查询</el-button></el-col>
                </el-row>
            </section>
            <section class="result-area" v-show="resultArr.length">
                <torrent-panel v-for="item of resultArr" v-bind:obj="item" :key="key" ></torrent-panel>
                <el-pagination
                        @current-change="handleCurrentChange"
                        layout="prev, pager, next"
                        :page-count="status.pages"
                        :total="status.total"
                        :current-page="status.currentPage">
                </el-pagination>
            </section>
            <section style="padding:120px 30px;" v-show="!resultArr.length">
                <h3>暂无查询结果</h3>
            </section>
        </div>
    </main>
</template>

<script>
    import TorrentPanel from "~/components/torrent-panel.vue";
    export default {
        name: "result",
        components: {TorrentPanel},
        data:function(){
            return {
                target:'',
                status:{
                    total:1,
                    pages:1,
                    currentPage:1
                },
                requestResult:'',
                resultArr:[]
            }
        },
        mounted: function () {
            if(this.$route.query['target']){
                this.target=this.$route.query['target'];
                this.page=this.$route.query['page'];
                this.btSearch();
            }else {
                this.$router.push({
                    name:'home'
                });
            }
        },
        methods:{
            handleCurrentChange:function(val){
                if(val!==this.status.currentPage){
                    this.status.currentPage=val;
                    this.btSearch();
                }
            },
            btSearch: function () {
                let _this=this;
                const loading = this.$loading({
                    lock: true,
                    text: 'Loading',
                });
                this.axios.get('/btSearch', {
                    params: {
                        target: this.target,
                        page:this.status.currentPage
                    }
                }).then((response) => {
                    setTimeout(()=>{
                        loading.close();
                    },100);
                    try {
                        let obj = {};
                        if (Object.prototype.toString.call(response['data']) === '[object Object]') {
                            obj = response['data']
                        }
                        else {
                            obj = JSON.parse(response['data']);
                        }
                        this.resultArr=obj.result;
                        obj.pages?this.status.pages=obj.pages:this.status.pages=1;
                        obj.totalLength?this.status.total=obj.totalLength:this.status.total=10;

                    } catch (err) {
                        console.log("parse error"+err);
                        setTimeout(()=>{
                            loading.close();
                            _this.alertError();
                        },100)
                    }
                }).catch((err)=>{
                    setTimeout(()=>{
                        console.log("catch error"+err);
                        loading.close();
                        _this.alertError();
                    },100)
                })
            },
            alertError: () => {
                alert("error!Interval sever error.");
            }
        }
    }
</script>

<style scoped>
    main{
        min-height: 100%;
        height: auto !important;
    }

    .container{
        padding: 80px 50px 30px 50px;
    }

    .input-area {
        padding: 0;
        max-width: 600px;
        min-height: 60px;
    }

    .result-area{
        padding-bottom: 100px;
    }

    @media only screen and (max-width: 768px) {
        .container{
            padding: 80px 0 30px;
        }
        .input-area {
            max-width: 360px;
            margin: 0 auto;
        }
    }
</style>


