<template>
    <div class="panel">
        <h3 class="title">
            {{obj.name}}
            <span class="hot" v-if="obj.queryTimes>5">
                HOT
            </span>
        </h3>
        <div class="content">
            <a class="magnet" :href="'magnet:?xt=urn:btih:'+obj.infoHash">
                [磁力链接]
            </a>
            <span>收录时间:<b>{{obj.time}}</b></span>
            <span>人气:<b>{{obj.queryTimes}}</b></span>
            <span>大小:<b>{{size}}</b></span>
            <br/>
            <div class="files" v-show="filePaths.length">
                <p>文件列表:</p>
                <ul v-on:click="showTrigger" style="cursor: pointer;">
                    <li v-for="file in showFilePaths">
                        {{file}}
                    </li>
                </ul>
                <div class="bottom" v-show="filePaths.length>showFilePaths.length">
                    <p v-on:click="showAll">
                        还有{{filePaths.length-showFilePaths.length}}项,点击全部展开
                        <img src="../assets/down-arrow.png"/>
                    </p>
                </div>
                <div class="bottom" v-show="(filePaths.length===showFilePaths.length)&&filePaths.length>5">
                    <p v-on:click="showLess">
                        <img style="transform: rotateX(180deg);" src="../assets/down-arrow.png"/>
                    </p>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    function formatSizeUnits(bytes) {
        if (bytes >= 1073741824) {
            bytes = (bytes / 1073741824).toFixed(2) + ' GB';
        }
        else if (bytes >= 1048576) {
            bytes = (bytes / 1048576).toFixed(2) + ' MB';
        }
        else if (bytes >= 1024) {
            bytes = (bytes / 1024).toFixed(2) + ' KB';
        }
        else if (bytes > 1) {
            bytes = bytes + ' bytes';
        }
        else if (bytes === 1) {
            bytes = bytes + ' byte';
        }
        else {
            bytes = '0 byte';
        }
        return bytes;
    }

    export default {
        name: "torrent-panel",
        props: ['obj'],
        data: function () {
            return {
                size: '',
                filePaths: [],
                showFilePaths: []
            }
        },
        mounted: function () {
            this.init();
        },
        methods: {
            init: function () {
                this.size = formatSizeUnits(this.obj.size);
                this.filePaths = this.obj.filePaths;
                this.showFilePaths = [];
                if (this.filePaths.length > 5) {
                    for (let i = 0; i < 5; i++) {
                        this.showFilePaths.push(this.filePaths[i]);
                    }
                } else {
                    this.showFilePaths = this.filePaths;
                }
            },
            showAll: function () {
                this.showFilePaths = this.filePaths;
            },
            showLess: function () {
                this.showFilePaths = [];
                if (this.filePaths.length > 5) {
                    for (let i = 0; i < 5; i++) {
                        this.showFilePaths.push(this.filePaths[i]);
                    }
                } else {
                    this.showFilePaths = this.filePaths;
                }
            },
            showTrigger:function(){
                this.showFilePaths.length===this.filePaths.length?this.showLess():this.showAll();
            }
        },
        watch: {
            obj: {
                deep: true,
                handler(val) {
                    this.init();
                }
            }
        }

    }
</script>

<style scoped>
    .panel {
        width: 80%;
        margin: 20px 0;
        padding: 15px 20px;
        border: 1px solid #eee;
        box-shadow: 0 0 5px #eee;
        border-radius: 3px;
    }

    .panel:hover {
        box-shadow: 3px 3px 10px #eee;
    }

    .title {
        font-size: 24px;
        text-align: left;
        color: #4abcdd;
    }

    .title .hot {
        border: 1px solid #d25565;
        color: #d25565;
        border-radius: 3px;
        padding: 4px 6px;
        font-size: 16px;
        vertical-align: middle;
    }

    a.magnet {
        text-decoration: none;
        color: #475F77;
        font-size: 16px;
        padding: 0 20px 0 0;
    }

    a.magnet:hover {
        text-decoration: none;
        color: #4abcdd;
    }

    .content {
        margin-top: 20px;
        color: #343434;
        font-size: 16px;
        text-align: left;
    }

    .content span {
        border-left: 1px solid #ccc;
        padding: 0 20px;
    }

    .content span b {
        color: #475F77;
    }

    .files {
        padding: 15px 0 0 0;
    }

    .files ul {
        padding: 0 20px;
    }

    .files li {
        font-size: 14px;
        list-style: none;
        color: #7d7d7d;
    }

    .files .bottom {
        margin-top: 10px;
        padding-top: 5px;
        border-top: 1px solid #eee;
        text-align: center;
        cursor: pointer;
    }

    .files .bottom p {
        border: 0;
    }

    .files .bottom p:hover {
        color: #4abcdd;
    }

    .files .bottom img {
        display: block;
        width: 32px;
        margin: 5px auto;
    }


    @media only screen and (max-width: 768px) {
        .panel{
            width: 80%;
            margin: 15px auto;
        }

        .title .hot {
            padding: 2px 3px;
            font-size: 12px;
        }
        .title{
            font-size: 16px;
        }
        .content{
            font-size: 14px;
        }
        .files li{
            font-size: 12px;
            max-width: 95%;
            text-align: justify;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        .files .bottom img{
            width: 24px;
        }
    }

</style>


