import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import Home from './pages/home.vue'
import Result from './pages/result.vue'

Vue.use(VueRouter);

export default new VueRouter({
    routes: [
        { path: '/', redirect:'home'},
        {
            path: '/home',
            name: 'home',
            component: Home
        },
        {
            path: '/result',
            name: 'result',
            component: Result
        }
    ]
})



