import Vue from 'vue'
import {
    Button,
    Menu,
    MenuItem,
    Row,
    Col,
    Input,
    Pagination,
    Progress,
    Loading
} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import router from "./router"
import "./js/rem"
import "./css/main.css"
import axios from "axios"

Vue.use(Button);
Vue.use(Row);
Vue.use(Col);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Input);
Vue.use(Pagination);
Vue.use(Progress);
Vue.prototype.$loading = Loading.service;
Vue.prototype.axios=axios;

new Vue({
    el: '#app',
    router,
    render:h => h(App)
});



