<template>
    <nav>
        <div class="logo"><img src="../assets/bthub-logo.png"></div>
        <el-menu :default-active="activeIndex" class="menu" mode="horizontal" @select="handleSelect">
            <el-menu-item index="home">Home</el-menu-item>
            <!--<el-menu-item index="about">About</el-menu-item>-->
        </el-menu>
    </nav>
</template>

<script>
    export default {
        name: "nav-bar",
        data() {
            return {
                activeIndex: 'home',
            };
        },
        methods: {
            handleSelect(key, keyPath) {
                this.$router.push({
                    name:keyPath
                })
            }
        }
    }
</script>

<style scoped>
    nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 60px;
        border-bottom: 1px solid #eee;
        background: #fff;
        z-index: 100;
    }
    .logo{
        line-height: 60px;
        font-size: 24px;
        height: 60px;
        padding: 0 0 0 20px;
        text-align: left;
    }
    .logo img{
        width: 200px;
    }
    .menu{
        position: absolute;
        right: 25px;
        top: 0;
    }
</style>


