<template>
    <main>
        <section class="bg">
            <img src="../assets/bthub.png"/>
        </section>
        <section class="input-area">
            <el-row>
                <el-col :span="18"><el-input type="text" v-model="target" placeholder="Input"></el-input></el-col>
                <el-col :span="6"> <el-button type="primary" v-on:click="btSearch">查询</el-button></el-col>
            </el-row>
        </section>
        <section class="gap">
            <div class="content">
                <h2>About us</h2>
                <p>
                    100000+种子收录,千万级别磁力链接
                    <br/>
                    Google力推的leveldb搭配作者瞎几把写的搜索引擎
                    <br/>
                    你值得拥有
                    <br/>
                    请记住我们的域名,www.bthub.info
                </p>
            </div>
        </section>
    </main>
</template>

<script>
    export default {
        name: "home",
        data: function () {
            return {
                target: "",
            }
        },
        methods: {
            btSearch: function () {
               this.$router.push({
                    name:'result',
                    query:{
                        target:this.target,
                        page:1
                    }
               })
            }
        }
    }
</script>

<style scoped>
    main {
        margin-top: 60px;
    }

    .bg{
        padding: 20px;
    }
    .bg img{
        max-width: 80%;
        margin: 0 auto;
    }

    .input-area {
        margin: 30px auto;
        padding: 0 20px;
        max-width: 600px;
        min-height: 60px;
    }



    .input-area h3 {
        font-size: 24px;
    }

    .gap {
        position: relative;
        background: url('../assets/low-poly-planet.png') no-repeat fixed;
        background-size: 100% 100%;
        width: 100%;
        height: 400px;
    }

    .gap .content {
        position: absolute;
        top: 3rem;
        left: 3rem;
        text-align: left;
        color: #fff;
    }

    .gap h2 {
        font-size: 28px;
    }

    .gap p {
        margin: 30px 0 0 0;
        max-width: 400px;
        font-size: 16px;
        line-height: 24px;
    }

    @media only screen and (max-width: 1450px) and (min-width: 769px) {

        .gap {
            position: relative;
            background: url('../assets/low-poly-planet_md.png') no-repeat fixed;
            background-size: 100% 100%;
            width: 100%;
            height: 500px;
        }

    }
    @media only screen and (max-width: 768px) {
        .gap h2 {
            font-size: 16px;
        }

        .gap {
            background: url('../assets/gap-mobile.jpg') no-repeat;
            background-size: cover;
            height: 360px;
        }

        .gap p {
            margin: 30px auto;
            max-width: 90%;
            font-size: 12px;
            line-height: 16px;
        }
    }
</style>


